import React, {Component} from 'react';
export default class Contact extends React.Component{
    constructor(){
        super();
    }
    render(){
        return(
            
                <form className="container mt-4 ">
                <div className="form-group">
    <label for="exampleInputName"/>Name
    <input type="name" className="form-control" id="exampleInputName" aria-describedby="nameHelp" placeholder="Enter Your Name Here"/>
    
  </div>
  <div className="form-group">
    <label for="exampleInputEmail1"/>Email
    <input type="email" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Enter Your Email Here"/>
    <small id="emailHelp" className="form-text text-muted">We'll never share your email with anyone else.</small>
  </div>
  <div className="form-group">
    <label for="exampleInputNumber1"/>Contact Number
    <input type="number" className="form-control" id="exampleInputNumber1" aria-describedby="emailHelp" placeholder="Enter Your Contact No. Here"/>
    
  </div>
  <div className="form-group">
    <label for="exampleFormControlTextarea1"/>Your Message
    <textarea className="form-control" id="exampleFormControlTextarea1" rows="3"/>
  </div>
  <button type="submit" className="btn btn-primary m-2">Submit</button>
</form>
            
        )
    }
}